/* styles.css */

.form-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  .form-card {
    border: 2px solid #007bff; /* Added border and adjusted color */
    border-radius: 10px; /* Increased border radius for a rounded look */
    padding: 20px;
    background-color: #fff; /* Added background color for the entire card */
  }
  
  .form-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .question-input {
    margin-bottom: 20px;
  }
  
  .input-field {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .add-button,
  .submit-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .add-button:hover,
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .error-message {
    color: red;
  }
  
  .question-answer-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
  }
  
  .answer-divider {
    border: none;
    height: 1px;
    background-color: #ccc;
    margin: 10px 0;
  }
  
  .question {
    font-weight: bold;
  }
  
  .answer-input {
    border: none;
    outline: none;
    width: 100%;
  }
  
  .question-card {
    position: relative;
    border: none; /* Removed border to avoid double border */
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .delete-button {
    position: absolute;
    bottom: 5px;
    right: 5px;
    background-color: #ff4d4f;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .delete-button:hover {
    background-color: #ff3333;
  }
  
  .delete-button:focus {
    outline: none;
  }
  
  .delete-icon {
    color: #fff;
    font-size: 16px;
  }
  
  .answer-field {
    margin-bottom: 20px; /* Adjusted to prevent overlap with the delete button */
  }
  
  .title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .description-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .add-button,
  .create-form-button {
    padding: 10px 20px;
    background-color: #4d8593;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .add-button:hover,
  .create-form-button:hover {
    background-color: #0056b3;
  }
  
  .add-button:focus,
  .create-form-button:focus {
    outline: none;
  }
  .plus-icon {
    margin-right: 5px; /* Adjust spacing between the icon and text if needed */
  }
  